@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    // TODO: заменить время на переменную из токенов для стилей
    --navbar-collapse-duration: 150ms;
    --navbar-width-collapsed: 72px;
    --navbar-width: 204px;
    --navbar-animation-hide-s: 0.15s;
    --navbar-animation-show-s: 0.3s;
    --navbar-width-mobile: 100%;
    --navbar-height: 100%;
    --navbar-height-mobile: 50px;

    display: flex;
    position: relative;

    flex-direction: column;
    align-items: center;

    will-change: width;

    // stylelint-disable-next-line plugin/no-low-performance-animation-properties
    transition: width ease-in-out var(--navbar-collapse-duration);

    padding-block-end: var(--ym-spacer-size-xl);

    width: var(--navbar-width);

    &_collapsed {
        width: var(--navbar-width-collapsed);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        transition: none;

        padding-block-start: 0;
        padding-block-end: 0;

        width: 100%;
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
