@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: grid;
    position: relative;
    grid-template:
        'navbar content' 1fr
        'navbar playerbar' auto / auto 1fr;

    column-gap: var(--ym-spacer-size-m);

    box-sizing: border-box;

    padding-block-start: var(--ym-spacer-size-m);
    padding-block-end: var(--ym-spacer-size-m);
    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-m);

    height: 100vh;

    &_applicationPreserveTitleBar {
        padding-block-start: var(--ym-spacer-size-xxxl);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding: 0;
    }

    @include mediaBreakpointDown(1080) {
        grid-template:
            'navbar content' 1fr
            'playerbar playerbar' auto / auto 1fr;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        grid-template:
            'content' 1fr
            'playerbar' auto
            'navbar' auto / 1fr;

        :global(.desktop) & {
            grid-template:
                'navbar content' 1fr
                'playerbar playerbar' auto / auto 1fr;
        }
    }
}

.content {
    position: relative;

    grid-area: content;

    &_withPlayerBar {
        margin-block-end: calc(var(--player-bar-height-mobile) - var(--ym-spacer-size-xxs));
    }
}

.notificationInfoContainer,
.notificationErrorContainer {
    position: absolute;

    inset-block-end: var(--ym-spacer-size-m);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        inset-block-end: var(--ym-spacer-size-xxl);

        width: 100%;
    }
}

.navbar {
    grid-area: navbar;

    &_application {
        &_macos {
            margin-block-start: var(--ym-spacer-size-xxl);
        }

        &_windows {
            margin-block-start: calc(-1 * var(--ym-spacer-size-l));
        }
    }
}

.playerBar {
    grid-area: playerbar;
}
