.root {
    display: flex;
    position: relative;

    flex-direction: column;
    align-items: center;

    width: 100%;
    height: 100%;
}

.collapseButton[data-important] {
    position: absolute;

    inset-inline-start: 50%;
    inset-block-start: 50%;

    z-index: var(--ym-z-index-header);

    transform: translate(-50%, -50%);

    transition: opacity var(--ym-duration-transition);

    opacity: 0;

    color: var(--ym-controls-color-secondary-on_outline-enabled_variant);

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-secondary-on_default-enabled);
    }
}

.feedbackGroup {
    margin-block-start: auto;
    margin-block-end: var(--ym-spacer-size-xxxl);
}

.logoLink {
    width: 100%;
}

.logo {
    display: flex;
    align-items: center;

    will-change: padding-inline-start;
    transition: padding-inline-start ease-in-out var(--navbar-collapse-duration);

    margin-block-end: var(--ym-spacer-size-xxl);

    cursor: pointer;
    padding-inline-start: var(--ym-spacer-size-l);

    width: 100%;

    min-height: 56px;

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.collapseButton:focus-visible {
    opacity: 1;
}

.logo:hover {
    .collapseButton {
        opacity: 1;
    }
}

.collapseButtonTooltip {
    &_hidden {
        opacity: 0;
    }
}

.title {
    opacity: 1;

    &_animate {
        animation: animation_show var(--navbar-animation-show-s) forwards;

        opacity: 0;

        @media (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    &_collapsed {
        opacity: 0;
    }
}

.title_animate.title_collapsed {
    animation: animation_hide var(--navbar-animation-hide-s) forwards;

    opacity: 1;

    @media (prefers-reduced-motion: reduce) {
        animation: none;
    }
}

.navigation {
    flex: 1;
}

.user {
    padding-inline-start: var(--ym-spacer-size-xl);

    width: 100%;
}

.userMeta {
    &_animate {
        animation: animation_show var(--navbar-animation-show-s) forwards;

        opacity: 0;

        @media (prefers-reduced-motion: reduce) {
            animation: none;
        }
    }

    &_collapsed {
        opacity: 0;
    }
}

.userMeta_animate.userMeta_collapsed {
    animation: animation_hide var(--navbar-animation-hide-s) forwards;

    opacity: 1;

    @media (prefers-reduced-motion: reduce) {
        animation: none;
    }
}

.rewindItem {
    &:hover,
    &:focus-visible {
        background-color: var(--ym-surface-color-primary-enabled-list);
    }
}

.rewindTitle {
    color: var(--ym-controls-color-primary-text-enabled_variant);
}

@keyframes animation_hide {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes animation_show {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}
