@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    --player-cover-width: 64px;
    --player-cover-height: 64px;

    display: grid;

    grid-template-columns: repeat(3, 33.3%);
    align-items: center;

    transition: background-color var(--ym-duration-transition), opacity 150ms ease-in;

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-l);

    background-color: var(--player-average-color-background, var(--ym-background-color-primary-enabled-player));

    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);

    width: 100%;
    height: var(--player-bar-height);

    overflow: hidden;

    &_disabled {
        opacity: 0.6;
    }

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.info {
    display: flex;
    align-items: center;
    justify-self: flex-start;

    width: 100%;

    gap: var(--ym-spacer-size-m);
}

.infoCard {
    display: flex;
    align-items: center;

    max-width: 70%;

    gap: var(--ym-spacer-size-xs);
}

.coverContainer {
    flex-shrink: 0;
}

.cover {
    width: var(--player-cover-width);
    height: var(--player-cover-height);
}

.description {
    display: flex;
    flex-direction: column;

    justify-content: center;

    width: 100%;
    overflow: hidden;
}

.artists {
    color: var(--ym-controls-color-secondary-text-enabled);
}

.artistLink {
    color: inherit;

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-secondary-text-hovered);
    }
}

.titleLink {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.infoButtons {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-xxxs);
}

.sonata {
    display: flex;
    flex-direction: column;
    justify-self: center;

    padding-inline-start: var(--ym-spacer-size-m);
    padding-inline-end: var(--ym-spacer-size-m);

    width: 120%;
}

.meta {
    display: flex;
    align-items: center;
    justify-self: flex-end;

    gap: var(--ym-spacer-size-xl);

    padding-inline-end: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown(1280) {
        gap: var(--ym-spacer-size-m);
    }
}

.titleContainer {
    display: flex;
    align-items: center;
}

.explicitMarkWrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    width: var(--ym-icon-size-s);
    height: var(--ym-icon-size-s);

    color: var(--ym-controls-color-secondary-text-enabled);
}
