.root {
    display: flex;
    align-items: center;

    gap: var(--ym-spacer-size-xs);

    max-width: 161px;
}

.icon {
    fill: var(--ym-controls-color-secondary-text-enabled);

    flex-shrink: 0;

    color: var(--ym-controls-color-secondary-text-enabled);
}

.button {
    flex-shrink: 0;

    &:hover,
    &:focus-visible {
        .icon {
            fill: var(--ym-controls-color-secondary-text-enabled_variant);

            color: var(--ym-controls-color-secondary-text-enabled_variant);
        }
    }
}
