.root {
    --player-bar-height: 66px;
    --player-cover-width: 48px;
    --player-cover-height: 48px;

    position: absolute;

    z-index: var(--ym-z-index-player);

    transform: translateY(-8px);

    transition: background-color var(--ym-duration-transition), opacity 150ms ease-in;

    margin-inline-start: var(--gap-between-contents);
    margin-inline-end: var(--gap-between-contents);

    border: 1px solid var(--ym-outline-color-primary-disabled);
    border-radius: var(--ym-radius-size-l);

    background-color: var(--player-average-color-background, var(--ym-background-color-primary-enabled-player));

    padding-block-start: var(--ym-spacer-size-xs);
    padding-block-end: var(--ym-spacer-size-xs);
    padding-inline-start: var(--ym-spacer-size-xs);
    padding-inline-end: var(--ym-spacer-size-xs);

    inset-block-end: 0;
    inset-inline-start: 0;

    width: calc(100% - 2 * var(--gap-between-contents));

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.progressBar {
    position: absolute;

    inset-inline-start: 50%;
    inset-block-start: 0;

    transform: translate(-50%, -50%);

    width: calc(100% - var(--ym-radius-size-l) * 2);
}

.info {
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
}

.infoCard {
    display: flex;

    gap: var(--ym-spacer-size-xs);
}

.coverContainer {
    flex-shrink: 0;
}

.cover {
    width: var(--player-cover-width);
    height: var(--player-cover-height);
}

.description {
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 100%;
    overflow: hidden;
}

.artists {
    color: var(--ym-controls-color-secondary-text-enabled);
}

.artistLink {
    color: inherit;

    &:hover,
    &:focus-visible {
        color: var(--ym-controls-color-secondary-text-hovered);
    }
}

.titleLink {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.infoButtons {
    display: flex;
    align-items: center;

    gap: var(--ym-spacer-size-xs);
}

.titleContainer {
    display: flex;
    align-items: center;
}

.explicitMarkWrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    width: var(--ym-icon-size-s);
    height: var(--ym-icon-size-s);

    color: var(--ym-controls-color-secondary-text-enabled);
}
