@import '~@yandex-music-int/sol/mixins/breakpoints.scss';

.root {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown(1280) {
        gap: var(--ym-spacer-size-l);
    }
}

.sonataButtons {
    display: flex;
    align-items: center;
    gap: var(--ym-spacer-size-xxxs);
}

.sonataButton {
    &:hover,
    &:focus-visible {
        transform: scale(1.05);
    }

    &:disabled {
        background-color: transparent;
    }
}

.playButtonIcon {
    color: var(--ym-controls-color-secondary-text-enabled_variant);
}
