.timecode {
    will-change: opacity;
    transition: opacity var(--ym-duration-transition);

    opacity: 0;

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.root {
    display: grid;
    grid-template: 'start slider end' auto / min-content auto min-content;

    align-items: center;

    column-gap: var(--ym-spacer-size-xs);

    &_mobile {
        gap: 0;
    }

    &_fullscreen {
        grid-template:
            'slider slider slider' auto
            'start auto end' auto / min-content auto min-content;
        column-gap: 0;
        row-gap: var(--ym-spacer-size-xs);
    }

    &:hover,
    &:focus-visible {
        .timecode {
            opacity: 1;
        }
    }
}

.slider {
    grid-area: slider;
}
