.root {
    will-change: max-height, opacity;

    // stylelint-disable-next-line plugin/no-low-performance-animation-properties
    transition: background-color var(--ym-duration-transition), max-height 500ms ease-in-out, opacity 500ms ease-in-out;

    opacity: 1;

    margin-block-start: var(--gap-between-contents);

    max-height: none;
    overflow: hidden;

    @media screen and (prefers-reduced-motion: reduce) {
        transition: none;
    }
}
